import React from "react";
import Img from "gatsby-image";

const Trabajo = ({ title, image, description, uri, filters }) => (
  <div
    id={title}
    className={
      "column is-4-desktop is-6-tablet is-12-mobile filter " +
      filters.toString().replace(/,/g, " ")
    }
  >
    <div className="card">
      <div className="card-image">
        <Img fluid={image.childImageSharp.fluid} alt={title} />
        <figcaption className="hover-text is-overlay is-vertical-center has-background-primary">
          <p className="pb-3 has-text-white">{description}</p>
          <a href={uri} className="button is-link is-outlined">
            Visitar Sitio <i className="pl-2 fa fa-arrow-right" />
          </a>
        </figcaption>
      </div>
    </div>
  </div>
);

export default Trabajo;
